<template>
  <div id="listRole">
    <h1 class="list-title">{{ title }}</h1>
    <div class="operate-box">
      <Button type="primary" icon="plus" size="large" @click="AddShow"></Button>
      <Button type="warning" icon="ios-reload" size="large" @click="RefreshList"></Button>
    </div>
    <div class="data-area">
      <Table :columns="UserCol"
             size="large"
             :row-class-name="IsSuperAdmin"
             :data="UserData"
             :loading="loading"></Table>
    </div>
    <Modal
      v-model="AddModal"
      title="">
      <Form ref="AddInfo" :model="AddInfo" :rules="ValidateRules" label-position="right" :label-width="80">
        <FormItem label="" prop="name">
          <Input v-model="AddInfo.name"></Input>
        </FormItem>
        <FormItem label="" prop="nickname">
          <Input v-model="AddInfo.nickname"></Input>
        </FormItem>
        <FormItem label="" prop="password">
          <Input v-model="AddInfo.password"></Input>
        </FormItem>
        <FormItem label="" prop="phone">
          <Input v-model="AddInfo.phone"></Input>
        </FormItem>
        <FormItem label="" prop="wechat">
          <Input v-model="AddInfo.wechat"></Input>
        </FormItem>
        <FormItem label="" prop="is_status">
          <RadioGroup v-model="AddInfo.is_status">
            <Radio label=""></Radio>
            <Radio label=""></Radio>
          </RadioGroup>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="text" @click="AddCancel" size="large"></Button>
        <Button type="primary" @click="AddOver" size="large"></Button>
      </div>
    </Modal>
    <Modal
      v-model="EditModal"
      title="">
      <Form ref="EditInfo" :model="EditInfo" :rules="ValidateRules" label-position="right" :label-width="60">
        <FormItem label="">
          <Input v-model="EditInfo.id" disabled></Input>
        </FormItem>
        <FormItem label="" prop="name">
          <Input v-model="EditInfo.name"></Input>
        </FormItem>
        <FormItem label="" prop="nickname">
          <Input v-model="EditInfo.nickname"></Input>
        </FormItem>
        <FormItem label="" prop="phone">
          <Input v-model="EditInfo.phone"></Input>
        </FormItem>
        <FormItem label="" prop="wechat">
          <Input v-model="EditInfo.wechat"></Input>
        </FormItem>
        <FormItem label="" prop="menu_index">
          <Select v-model="EditInfo.menu_index">
            <Option v-for="item in AuthData" :value="item.id" :key="item.id">{{ item.title }}</Option>
          </Select>
        </FormItem>
        <FormItem label="" prop="is_status">
          <RadioGroup v-model="EditInfo.is_status">
            <Radio label=""></Radio>
            <Radio label=""></Radio>
          </RadioGroup>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="text" @click="EditCancel" size="large"></Button>
        <Button type="primary" @click="EditOver" size="large"></Button>
      </div>
    </Modal>
    <RadioModal
      title=""
      :modalShow="AssignDepModal"
      :RdoInfo="DeparList"
      :InitDep="InitDep"
      label1=""
      label2=""
      @UpOver="Updepart"
      @CloseModal="CloseDep"></RadioModal>
    <CheckboxModal
      title=""
      :modalShow="AssignRoleModal"
      :CheckInfo="RoleList"
      :InitData="InitRole"
      label1=""
      label2=""
      @UpOver="UpRole"
      @CloseModal="CloseRole"></CheckboxModal>
    <CheckboxModal
      title=""
      :modalShow="SeeDepModal"
      :CheckInfo="SeeDepList"
      :InitData="InitSeeDep"
      label1=""
      label2=""
      @UpOver="UpSeeDep"
      @CloseModal="CloseSeeDep"></CheckboxModal>
  </div>
</template>

<script>
  import { getLocal } from '@/util/util'
  import { phoneCheck } from '@/util/valid'
  import RadioModal from '@/components/listUser/RadioModal'
  import CheckboxModal from '@/components/listUser/CheckboxModal'

  export default {
    name: 'listRole',
    components: {
      RadioModal,
      CheckboxModal
    },
    data () {
      return {
        title: '',
        DeparList: [],
        RoleList: [],
        SeeDepList: [],
        AssignDepModal: false,
        InitDep: {
          id: '',
          depar_id: ''
        },
        AssignRoleModal: false,
        InitRole:{
          id: '',
          check_id: []
        },
        SeeDepModal: false,
        InitSeeDep:{
            id: '',
            check_id: []
        },
        loading: true,
        //
        UserCol: [
          {
            title: '',
            width: '100',
            key: 'id'
          },{
            title: '',
            width: '100',
            key: 'admin_name'
          },{
            title: '',
            key: 'nickname'
          },{
            title: '',
            key: 'phone'
          },{
            title: '',
            key: 'menu_index'
          },{
            title: '',
            width: '100',
            key: 'depar_id'
          },{
            title: '',
            key: 'wechat'
          },{
            title: '',
            key: 'recent_logintime'
          },{
            title: '',
            key: 'role'
          },{
            title: '',
            key: 'depar'
          },{
            title: '',
            key: 'operation',
            align: 'center',
            width: '280',
            render: (h, params)=>{
              let BtnArr = [];
              if(params.row.id === 1){
                BtnArr = ''
              }else{
                let rule = {};
                rule.btns = [{
                  class: 'ChangeStatus',
                  name: 'is_status',
                  type: 'warning',
                  right: '',
                  wbtn: ''
                }];
                rule.size = 2;
                BtnArr = this.$renderBtn(h, params, this.BtnData,rule);
              }
              return h('div',BtnArr);
            }
          }
        ],
        UserData: [],     //
        RowUserData: [],  //
        BtnData: [],      //
        AuthData: [],
        EditModal: false,
        //
        EditInfo:{
          id: '',
          name: '',
          nickname: '',
          phone: '',
          wechat: '',
          menu_index: '',
          is_status: ''
        },
        AddModal: false,
        //
        AddInfo: {
          name: '',
          nickname: '',
          password: '',
          phone: '',
          wechat: '',
          is_status: ''
        },
        ValidateRules:{
          name: [
            {required: true,message: ''}
          ],
          nickname: [
            {required: true,message: ''}
          ],
          password: [
            {required: true,message: ''}
          ],
          phone: [
            {required: true, message: ''},
            {validator: phoneCheck, trigger: 'blur'}
          ],
          wechat: [
            {required: true,message: ''}
          ],
          is_status: [
            {required: true,message: ''}
          ],
          menu_index: [
            {required: true,message: ''}
          ]
        }
      }
    },
    created(){
      this.InitData();
    },
    methods: {
      //
      InitData(callback = ()=>{}){
        const that = this;
        const auth_id = getLocal('auth_id');
        this.loading = true;
        //
        this.$post('/backend/Auth/adminUserList').then((d)=>{
          let info = d.data;
          const auth_id = getLocal('auth_id');
          that.RowUserData = info;
          info.forEach((infoObj)=>{
            infoObj.is_status = infoObj.is_status?'':'';
          });
          that.UserData = info;
          that.loading = false;
          callback();
        });
        //
        this.$post('/backend/Auth/deparList').then((d)=>{
          that.DeparList = d.data;
          that.SeeDepList = [];
          d.data.forEach((val,index)=>{
            let data = {
              id: val.id,
              role_name: val.title
            };
            that.SeeDepList.push(data);
          })
        });
        //
        this.$post('/backend/Auth/roleList').then((d)=>{
          that.RoleList = d.data;
        });
        //
        this.$fetch('/backend/Menuauth/listAuthGet',{auth_id}).then((d)=>{
          this.BtnData = d.data.operation;
        })
      },
      //
      IsSuperAdmin(row){
        if(row.id === 1){
          return 'super-admin';
        }else{
          return '';
        }
      },
      //
      RefreshList(){
        this.InitData(()=>{
          this.$Message.success('');
        });
      },
      //
      UploadData(url,info){
        return new Promise((resolve)=>{
          if(info.is_status) info.is_status = info.is_status === ''?1:0;
          this.$post(url,info).then((d)=>{
            if(d.status === 1){
              this.$Message.success(d.message);
              resolve();
              this.InitData();
            }else{
              this.$Message.error(d.message);
            }
          }).catch((err)=>{
            this.$Message.error('');
          })
        })
      },
      //
      AddShow(){
        this.$refs['AddInfo'].resetFields();
        for(let key in this.AddInfo){
            this.AddInfo[key] = '';
        }
        this.AddModal = true;
      },
      //
      AddCancel(){
        this.AddModal = false;
      },
      AddOver(){
        this.$refs['AddInfo'].validate((valid)=>{
          if(valid){
            this.UploadData('/backend/Auth/adminUserAdd',this.AddInfo).then(()=>{
              this.AddModal = false;
            });
          }
        });
      },
      //
      EditOpt(row){
        for(let key in this.EditInfo){
          if(key === 'name'){
            this.EditInfo['name'] = row['admin_name'];
          }else{
            this.EditInfo[key] = row[key]
          }
        }
        this.$fetch('/backend/Auth/getUseAuth',{admin_id: row.id}).then(d=>{
          this.EditModal = true;
          this.AuthData = d.data;
          this.AuthData.forEach(val=>{
            if(val.title === this.EditInfo.menu_index){
              this.EditInfo.menu_index = val.id;
            }
          });
        });
      },
      EditCancel(){
        this.EditModal = false;
      },
      EditOver(){
        this.$refs['EditInfo'].validate(valid=>{
          if(valid){
            this.UploadData('/backend/Auth/adminUserUpdate',this.EditInfo).then(()=>{
              this.EditModal = false;
            })
          }
        });
      },
      //
      ChangeStatus(row){
        this.UploadData('/backend/Auth/adminUserStatus',{admin_id: row.id});
      },
      //
      AssignDep(row){
        this.InitDep.id = row.id;
        this.InitDep.depar_id = row.depar_id;
        this.AssignDepModal = true;
      },
      CloseDep(){
        this.AssignDepModal = false;
      },
      Updepart(info){
        this.AssignDepModal = false;
        let data = {
          admin_id: info.id,
          depar_id: info.depar_id
        };
        this.UploadData('/backend/Auth/deparAdminUser',data);
      },
      //
      AssignRole(row){
        this.InitRole.id = row.id;
        if(row.role !== ''){
          this.InitRole.check_id = row.role.toString().split(',').map((val)=>{
              return parseInt(val);
          });
        }else{
          this.InitRole.check_id = [];
        }
        this.AssignRoleModal = true;
      },
      CloseRole(){
        this.AssignRoleModal = false;
      },
      UpRole(info){
        this.AssignRoleModal = false;
        let data = {
          admin_id: info.id,
          role_id: info.check_id.join(',')
        };
        this.UploadData('/backend/Auth/roleAllotment',data);
      },
      //
      SeeDep(row){
        this.InitSeeDep.id = row.id;
        if(row.depar !== ''){
          this.InitSeeDep.check_id = row.depar.toString().split(',').map((val)=>{
            return parseInt(val);
          });
        }else{
          this.InitSeeDep.check_id = [];
        }
        this.SeeDepModal = true;
      },
      CloseSeeDep(){
        this.SeeDepModal = false;
      },
      UpSeeDep(info){
        this.SeeDepModal = false;
        let data = {
          admin_id: info.id,
          depar_id: info.check_id.join(',')
        };
        this.UploadData('/backend/Auth/dataScope',data);
      }
    }
  }
</script>


<style lang="less" scoped>
  .list-title{
    font-weight: normal;
  }
  .operate-box{
    padding: 10px 0 20px;
  }
</style>



