<template>
  <div id="workliu-list">
    <h1 class="list-title">
      <span class="tit-text">{{ title }}</span>
      <Button class="tit-btn"
              type="info"
              icon="ios-reload"
              size="large"
              @click="RefreshList"></Button>
    </h1>
    <div class="screen-area">
      <Card>
        <div class="card-tit" slot="title">
          <h3>
            <Icon type="ios-pricetags-outline"></Icon>
            
          </h3>
          <div class="btn-box">
            <Button type="ghost" icon="reply" @click="ResetScreen"></Button>
            <Button type="success" icon="search" @click="SimpleSearch"></Button>
          </div>
        </div>
        <div class="opt-box">
          <Form :model="ScreenData" inline :label-width="85">
            <FormItem label="">
              <Input v-model="ScreenData.name" style="width: 120px"></Input>
            </FormItem>
            <FormItem label="">
              <Input v-model="ScreenData.phone" style="width: 120px"></Input>
            </FormItem>
            <FormItem label="">
              <DatePicker type="datetimerange"
                          placeholder=""
                          format="yyyy-MM-dd HH:mm:ss"
                          placement="bottom-end"
                          :value="allTime"
                          @on-change="PickDate"
                          style="width: 280px"></DatePicker>
            </FormItem>
            <FormItem label="">
              <Select v-model="ScreenData.status" style="width:162px">
                <Option :value="0"></Option>
                <Option :value="1"></Option>
                <Option :value="-1"></Option>
              </Select>
            </FormItem>
          </Form>
        </div>
      </Card>
    </div>
    <div class="data-area">
      <Card :padding="0">
        <div class="card-tit" slot="title">
          <h3>
            <Icon type="clipboard"></Icon>
            
          </h3>
          <div class="btn-box">
            <!--<Button type="primary" size="large" icon="archive" @click="ExportData"></Button>-->
          </div>
        </div>
        <Table :columns="UserCol"
               :data="UserData"
               :loading="loading"
               @on-selection-change="SelectTable"></Table>
        <div class="page-box">
          <Page :current="Page.cur"
                :page-size="Page.size"
                :total="Page.count"
                placement="top"
                @on-change="ChangePage"
                @on-page-size-change="ChangeSize" show-sizer></Page>
        </div>
      </Card>
    </div>
    <AuditModal :modalShow="Audit.modal"
                :InitId="Audit.id"
                :UniqueId="Audit.id"
                :AllId="Audit.allId"
                @CloseModal="AuditCancel"></AuditModal>
  </div>
</template>

<script>
  import { getLocal } from '@/util/util'
  import GroupSms from '@/components/groupModal/GroupSms'
  import AuditModal from '@/components/infoModal/AuditModal'

  export default {
    name: 'WorkLiuList',
    components:{
      GroupSms,
      AuditModal
    },
    data () {
      return {
        title: '',
        apiUrl: '/backend/Workliu/workliuList',
        auth_id: '',
        loading: true,
        Remark: {
          modal: false,
          loan_id: '',
          remark: ''
        },
        allTime: [],
        //
        ScreenData: {
          name: '',
          phone: '',
          start_time: '',
          end_time: '',
          status: ''
        },
        UserCol: [
          {
            title: '',
            width: '70',
            align: 'center',
            key: 'id'
          },{
            title: '',
            width: '100',
            align: 'center',
            key: 'name'
          },{
            title: '',
            width: '150',
            align: 'center',
            key: 'phone'
          },{
            title: '',
            key: 'amount'
          },{
            title: '',
            key: 'request_date'
          },{
            title: '',
            key: 'status'
          },{
            title: '',
            key: 'operation',
            align: 'center',
            width: '330',
            render: (h, params)=>{
              return h('div',this.$renderBtn(h, params, this.BtnData));
            }
          }
        ],
        UserData: [],     //
        BtnData: [],
        RowUserData: [],  //
        //
        SelectData: [],
        Group: {
          SmsModal: false,
          AppmsgModal: false
        },
        //
        Page: {
          count: 0,
          cur: 1,
          size: 20,
        },
        //
        Audit:{
          modal: false,
          id: '',
          allId: ''
        }
      }
    },
    created(){
      this.auth_id = getLocal('auth_id');
      this.InitData(this.apiUrl);
    },
    methods: {
      //data
      RemoveObserve(rowdata){
        return JSON.parse(JSON.stringify(rowdata));
      },
      //()
      ResetScreen(){
        for(let key in this.ScreenData){
          this.ScreenData[key] = '';
        }
        this.allTime = '';
      },
      //
      SelectTable(data){
        let idarr = [];
        if(data.length > 0){
          data.forEach(val=>{
            idarr.push(val.id);
          })
        }
        this.SelectData = idarr;
      },
      //
      PickDate(time){
        this.allTime = time;
      },
      //
      SimpleSearch(sign = 1){
        let sinfo = this.RemoveObserve(this.ScreenData);
        if(this.allTime[0] !== ""){
          sinfo.start_time = this.allTime[0];
          sinfo.end_time = this.allTime[1];
        }else{
          sinfo.start_time = '';
          sinfo.end_time = '';
        }
        this.InitData(this.apiUrl,sinfo).then(()=>{
          if(sign){
            this.$Message.success('')
          }
        });
      },
      //
      InitData(url,params = {}){
        const that = this;
        this.loading = true;
        //
        this.$fetch("/backend/Menuauth/listAuthGet",{auth_id: this.auth_id}).then((d)=>{
          this.BtnData = d.data.operation;
        });
        //
        return new Promise((resolve)=>{
          this.$post(url,params).then((d)=>{
            let res = d.data.list;
            this.Page.count = d.data.count;
            this.UserData = res;
            this.RowUserData = res;
            that.loading = false;
            resolve();
          })
        })
      },
      //
      RefreshList(){
        this.InitData(this.apiUrl).then(()=>{
          this.$Message.success('');
        });
      },
      //
      UploadData(url,info){
        return new Promise((resolve)=>{
          this.$post(url,info).then((d)=>{
            if(d.status === 1){
              this.$Message.success(d.message);
              resolve(d.data);
            }else{
              this.$Message.error(d.message);
            }
          }).catch((err)=>{
            this.$Message.error('');
          })
        })
      },
      //
      DetailsOpt(row){
        this.Audit.modal = true;
        this.Audit.id = row.id;
        let idArr = [];
        this.RowUserData.forEach(val=>{
          idArr.push(val.id);
        });
        this.Audit.allId = idArr;
      },
      AuditCancel(){
        this.Audit.modal = false;
      },
      //
      Delopt(row){

      },
      //
      LoanOpt(row){
        let tips = '';
        this.$Modal.confirm({
          title: '',
          content: `<p class="confirm-text">${tips}</p>`,
          onOk: ()=>{
            this.UploadData('/backend/Workliu/workliuLending',{jid: row.id}).then(()=>{
              this.SimpleSearch(0);
            });
          }
        })
      },
      //
      RejectOpt(row){
        this.$Modal.confirm({
          title: '',
          content: `<p class="confirm-text"></p>`,
          onOk: ()=>{
            this.UploadData('/backend/Workliu/workliuReject',{jid: row.id}).then(()=>{
              this.SimpleSearch(0);
            });
          }
        })
      },
      //
      ExportData(){
        let sinfo = this.RemoveObserve(this.ScreenData);
        sinfo.expro = 1;
        this.UploadData(this.apiUrl,sinfo).then((url)=>{
          console.log(url);
          //window.location.href = url;
        });
      },
      //
      ChangePage(curpage){
        let sinfo = Object.assign(this.ScreenData,{
          page: curpage,
          num: this.Page.size
        });
        this.InitData(this.apiUrl,sinfo).then(()=>{
          this.Page.cur = curpage;
        })
      },
      //
      ChangeSize(size){
        let sinfo = Object.assign(this.ScreenData,{
          page: 1,
          num: size
        });
        this.InitData(this.apiUrl,sinfo).then(()=>{
          this.Page.cur = 1;
          this.Page.size = size;
        })
      }
    }
  }
</script>


<style lang="less" scoped>
  .list-title{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    font-weight: normal;
    padding-bottom: 10px;
  }
  .data-area{
    padding-top: 20px;
    .ivu-table-wrapper{
      border: none;
    }
    .page-box{
      padding: 20px;
      display: flex;
      flex-direction: row-reverse;
    }
  }
  .card-tit{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }

</style>



