<template>
  <div id="listRole">
    <h1 class="list-title">{{ title }}</h1>
    <div class="operate-box">
      <Button type="primary" icon="plus" size="large" @click="AddOpt({levelNum: 0})"></Button>
      <Button type="warning" icon="ios-reload" size="large" @click="RefreshList"></Button>
    </div>
    <div class="data-area">
      <Table :columns="UserCol"
             size="large"
             :data="DepData"
             :loading="loading"></Table>
    </div>
    <Modal
      v-model="AddModal"
      title="">
      <Form ref="AddInfo" :model="AddInfo" :rules="ValidateRules" label-position="right" :label-width="80">
        <FormItem label="" prop="title">
          <Input v-model="AddInfo.title"></Input>
        </FormItem>
        <FormItem label="" prop="status">
          <RadioGroup v-model="AddInfo.status">
            <Radio label=""></Radio>
            <Radio label=""></Radio>
          </RadioGroup>
        </FormItem>
        <FormItem label="" prop="sort">
          <Input v-model="AddInfo.sort"></Input>
        </FormItem>
        <FormItem label="">
          <Input v-model="AddInfo.icon" :disabled="AddInfo.iconState"></Input>
        </FormItem>
        <FormItem label="">
          <Input v-model="AddInfo.function" :disabled="AddInfo.topLevel"></Input>
        </FormItem>
        <FormItem label="">
          <Input v-model="AddInfo.apiurl" :disabled="AddInfo.topLevel"></Input>
        </FormItem>
        <FormItem label="">
          <RadioGroup v-model="AddInfo.mark">
            <Radio label="1"></Radio>
            <Radio label="0"></Radio>
          </RadioGroup>
        </FormItem>
        <FormItem label="">
          <Select v-model="AddInfo.href" :disabled="AddInfo.hideBtn" placeholder="">
            <Option value="DetailsOpt"></Option>
            <Option value="Delopt"></Option>
            <Option value="AddOpt"></Option>
            <Option value="EditOpt"></Option>
            <Option value="RemarkOpt"></Option>
            <Option value="DelayOpt"></Option>
            <Option value="MarkOpt"></Option>
            <Option value="LoanOpt"></Option>
            <Option value="QuitOpt"></Option>
            <Option value="RejectOpt"></Option>
            <Option value="AuditPanel"></Option>
            <Option value="ChangeStatus"></Option>
            <Option value="AssignUrge"></Option>
            <Option value="AssignAuth"></Option>
            <Option value="AssignDep"></Option>
            <Option value="AssignRole"></Option>
            <Option value="SeeDep"></Option>
          </Select>
        </FormItem>
        <FormItem label="">
          <Select v-model="AddInfo.color" :disabled="AddInfo.hideBtn" placeholder="">
            <Option value="ghost"></Option>
            <Option value="primary"></Option>
            <Option value="info"></Option>
            <Option value="success"></Option>
            <Option value="warning"></Option>
            <Option value="error"></Option>
          </Select>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="text" @click="AddCancel" size="large"></Button>
        <Button type="primary" @click="AddOver" size="large"></Button>
      </div>
    </Modal>
    <Modal
      v-model="EditModal"
      title="">
      <Form ref="EditInfo" :model="EditInfo" :rules="ValidateRules" label-position="right" :label-width="80">
        <FormItem label="" prop="title">
          <Input v-model="EditInfo.title"></Input>
        </FormItem>
        <FormItem label="" prop="status">
          <RadioGroup v-model="EditInfo.status">
            <Radio label=""></Radio>
            <Radio label=""></Radio>
          </RadioGroup>
        </FormItem>
        <FormItem label="" prop="sort">
          <Input v-model="EditInfo.sort"></Input>
        </FormItem>
        <FormItem label="">
          <Input v-model="EditInfo.icon" :disabled="EditInfo.iconState"></Input>
        </FormItem>
        <FormItem label="">
          <Input v-model="EditInfo.function" :disabled="EditInfo.topLevel"></Input>
        </FormItem>
        <FormItem label="">
          <Input v-model="EditInfo.apiurl" :disabled="EditInfo.topLevel"></Input>
        </FormItem>
        <FormItem label="">
          <RadioGroup v-model="EditInfo.mark">
            <Radio label="1"></Radio>
            <Radio label="0"></Radio>
          </RadioGroup>
        </FormItem>
        <FormItem label="">
          <Select v-model="EditInfo.href" :disabled="EditInfo.hideBtn" placeholder="">
            <Option value="DetailsOpt"></Option>
            <Option value="Delopt"></Option>
            <Option value="AddOpt"></Option>
            <Option value="EditOpt"></Option>
            <Option value="RemarkOpt"></Option>
            <Option value="DelayOpt"></Option>
            <Option value="MarkOpt"></Option>
            <Option value="LoanOpt"></Option>
            <Option value="QuitOpt"></Option>
            <Option value="RejectOpt"></Option>
            <Option value="AuditPanel"></Option>
            <Option value="ChangeStatus"></Option>
            <Option value="AssignUrge"></Option>
            <Option value="AssignAuth"></Option>
            <Option value="AssignDep"></Option>
            <Option value="AssignRole"></Option>
            <Option value="SeeDep"></Option>
          </Select>
        </FormItem>
        <FormItem label="">
          <Select v-model="EditInfo.color" :disabled="EditInfo.hideBtn" placeholder="">
            <Option value="ghost"></Option>
            <Option value="primary"></Option>
            <Option value="info"></Option>
            <Option value="success"></Option>
            <Option value="warning"></Option>
            <Option value="error"></Option>
          </Select>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="text" @click="EditCancel" size="large"></Button>
        <Button type="primary" @click="EditOver" size="large"></Button>
      </div>
    </Modal>

  </div>
</template>

<script>
  import { getLocal } from '@/util/util'
  import store from '@/store'

  export default {
    name: 'listRole',
    data () {
      return {
        title: '',
        auth_id: '',
        loading: true,
        UserCol: [
          {
            title: '',
            key: 'id',
            width: '100'
          },{
            title: '',
            key: 'title',
            width: '180'
          },{
            title: '',
            key: 'icon'
          },{
            title: '',
            key: 'function'
          },{
            title: '',
            key: 'apiurl'
          },{
            title: '',
            key: 'href'
          },{
            title: '',
            width: '80',
            key: 'status'
          },{
            title: '',
            width: '80',
            key: 'sort'
          },{
            title: '',
            key: 'operation',
            align: 'center',
            width: '220',
            render: (h, params)=>{
              let BtnArr = this.$renderBtn(h, params, this.BtnData);
              if(!params.row.hasAdd){
                BtnArr.shift();
              }
              return h('div',BtnArr);
            }
          }
        ],
        DepData: [],     //
        RowDepData: [],  //
        BtnData: [],      //
        ValidateRules: {
          title: [
            {required: true,message: ''}
          ],
          status: [
            {required: true,message: ''}
          ],
          sort: [
            {required: true,message: ''}
          ]
        },
        EditModal: false,
        EditInfo:{
          title: '',
          id: '',
          icon: '',
          function: '',
          apiurl: '',
          href: '',
          hideBtn: false,
          topLevel: false,
          iconState: false,
          status: '',
          sort: '',
          mark: 1,
          color: ''
        },
        AddModal: false,
        AddInfo: {
          pid: '',
          title: '',
          icon: '',
          function: '',
          apiurl: '',
          href: '',
          hideBtn: false,
          topLevel: false,
          iconState: false,
          status: '',
          sort: '',
          mark: 0,
          color: ''
        },
        AssignModal: false,
        AssignInfo:{
          role_id: '',
          auth_id: ['']
        },
        AuthList: []
      }
    },
    created(){
      this.auth_id = getLocal('auth_id');
      this.InitData();
    },
    methods: {
      //
      InitData(callback = ()=>{}){
        const that = this;
        this.loading = true;
        //
        this.$fetch('/backend/Menuauth/listAuthGet',{auth_id: this.auth_id}).then((d)=>{
          this.BtnData = d.data.operation;
        })
        //
        this.$post('/backend/Auth/authList').then((d)=>{
          let info = d.data;
          let res = [];
          that.RowDepData = info;
          this.DepData = [];
          this.GetChild(info, this.DepData);
          //this.$store.dispatch('setView');
          that.loading = false;
          callback();
        });
      },
      //
      GetChild(data,arr,num = 0) {
        num++;
        data.forEach((val)=>{
          val.levelNum = num;
          let space = '';
          if(num > 1){
              for(let i = 1;i<num;i++){
                space += "";
              }
              val.title = space + "|" + val.title;
          }
          if(num > 2){
              val.hasAdd = false;
          }else{
              val.hasAdd = true;
          }
          val.status = (val.status === 1)?'':'';
          arr.push(val);
          if(val.auth.length > 0){
            this.GetChild(val.auth,arr,num);
          }
        })
      },
      //
      RefreshList(){
        this.InitData(()=>{
          this.$Message.success('');
        });
      },
      //
      UploadData(url,info){
        return new Promise(resolve=>{
          if(info.status) info.status = (info.status === '')?1:0;
          this.$post(url,info).then((d)=>{
            if(d.status === 1){
              this.$Message.success(d.message);
              resolve();
              this.InitData();
            }else{
              this.$Message.error(d.message);
            }
          }).catch((err)=>{
            this.$Message.error('');
          })
        });
      },
      //
      AddOpt(p){
        this.$refs['AddInfo'].resetFields();
        this.AddInfo.href = '';
        for(let key in this.AddInfo){
          this.AddInfo[key] = '';
        }
        this.AddInfo.mark = 0;
        this.AddInfo.iconState = true;
        this.AddInfo.topLevel = true;
        if(p.levelNum === 0){
          this.AddInfo.hideBtn = true;
          this.AddInfo.iconState = false;
          this.AddInfo.pid = 0;
        }else if(p.levelNum === 1){
          this.AddInfo.topLevel = false;
          this.AddInfo.pid = p.id;
        }else{
          if(p.levelNum === 2){
            this.AddInfo.mark = 1;
            this.AddInfo.hideBtn = false;
          }else{
            this.AddInfo.hideBtn = true;
          }
          this.AddInfo.pid = p.id;
        }
        this.AddModal = true;
      },
      AddCancel(){
        this.AddModal = false;
      },
      //
      AddOver(){
        this.$refs['AddInfo'].validate((valid)=>{
          if(valid){
            this.UploadData('/backend/Auth/authAdd',this.AddInfo).then(()=>{
              this.AddModal = false;
            });
          }
        });
      },
      //
      EditOpt(row){
        this.EditModal = true;
        console.log(row.levelNum);
        for(let key in this.EditInfo){
          if(key === 'title'){
              let title = row['title'];
              if(title.indexOf('') !== -1){
                title = title.slice(title.indexOf('') + 2);
              }
              this.EditInfo['title'] = title;
          }else if(key === 'hideBtn'){
            this.EditInfo.hideBtn = (row.levelNum === 3)?false:true;
          }else if(key === 'mark'){
            this.EditInfo.mark = (row.levelNum === 3)?1:0;
          }else{
            this.EditInfo[key] = row[key];
          }
        }
        this.EditInfo.iconState = true;
        this.EditInfo.topLevel = true;
        this.EditInfo.hideBtn = true;
        switch (row.levelNum) {
          case 1:
            this.EditInfo.iconState = false;
            break;
          case 2:
            this.EditInfo.topLevel = false;
            break;
          case 3:
            this.EditInfo.hideBtn = false;
            break;
        }
      },
      EditCancel(){
        this.EditModal = false;
      },
      //
      EditOver(){
        this.$refs['EditInfo'].validate(valid=>{
          if(valid){
            this.UploadData('/backend/Auth/authUp',this.EditInfo).then(()=>{
              this.EditModal = false;
            });
          }
        });
      },
      //
      Delopt(row){
        //
        this.$Modal.confirm({
          title: '',
          content: '<p class="confirm-text"></p>',
          onOk: ()=>{
            this.UploadData('/backend/Auth/authDel',{id: row.id});
          }
        })
      }
    }
  }
</script>


<style lang="less" scoped>
  .list-title{
    font-weight: normal;
  }
  .operate-box{
    padding: 10px 0 20px;
  }

</style>



